var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"page-container"},[_c('el-breadcrumb',[_c('el-breadcrumb-item',{attrs:{"to":{ path: '/' }}},[_vm._v("首頁")]),_vm._v(" "),_c('el-breadcrumb-item',[_vm._v("服務項目")]),_vm._v(" "),_c('el-breadcrumb-item',[_vm._v("美學微整")]),_vm._v(" "),_c('el-breadcrumb-item',[_vm._v("洢蓮絲")])],1),_vm._ssrNode(" "),_c('Title',{attrs:{"title":"洢蓮絲重返少女肌","titleEn":"ELLANSE"}}),_vm._ssrNode(" <div class=\"article-container service-page\"><picture><source"+(_vm._ssrAttr("srcset",require("assets/img/service/beautyService/ellanse/banner.webp")))+" type=\"image/webp\"> <img"+(_vm._ssrAttr("src",require("assets/img/service/beautyService/ellanse/banner.jpg")))+" alt=\"洢蓮絲\"></picture> <h2>洢蓮絲</h2> <iframe src=\"https://www.youtube.com/embed/mgnff0VUi6A\" title=\"YouTube video player\" frameborder=\"0\" allow=\"accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture\" allowfullscreen=\"allowfullscreen\"></iframe></div> <div class=\"article-container\"><h2>Ellanse 洢蓮絲原理</h2> <p>膠原蛋白增生，效果持續</p> <p>微聚脂晶球隨著時間會刺激膠原蛋白的新生微肌膚持續搭建澎潤的3D填補結構，有效提供1-2年的撫平皺紋效果</p> <div class=\"img-block\"><div class=\"img-block-item\"><picture><source"+(_vm._ssrAttr("srcset",require("assets/img/service/beautyService/ellanse/1.webp")))+" type=\"image/webp\"> <img"+(_vm._ssrAttr("src",require("assets/img/service/beautyService/ellanse/1.png")))+" alt=\"Ellanse 洢蓮絲原理\"></picture></div> <div class=\"img-block-item\"><picture><source"+(_vm._ssrAttr("srcset",require("assets/img/service/beautyService/ellanse/2.webp")))+" type=\"image/webp\"> <img"+(_vm._ssrAttr("src",require("assets/img/service/beautyService/ellanse/2.png")))+" alt=\"Ellanse 洢蓮絲原理\"></picture></div></div></div> <div class=\"article-container\"><h2>組合</h2> <ul><li>聚已內酯(polycaprolactone,PCL)製成的25-50微米(pu)的完美微行晶球</li> <li>PBS-生物降解材料(carboxymethylcellulose,CMC)製成的凝膠體</li></ul> <picture><source"+(_vm._ssrAttr("srcset",require("assets/img/service/beautyService/ellanse/ellanse-組合1.webp")))+" type=\"image/webp\"> <img"+(_vm._ssrAttr("src",require("assets/img/service/beautyService/ellanse/ellanse-組合1.jpg")))+" alt=\"Ellanse 組合示意圖\" class=\"w-50\"></picture></div> <div class=\"article-container\"><h2>PCL&amp;CMC</h2> <ul><li>數十年來，PCL已被廣泛運用在各種可被人體吸收的植體上(如:外科縫線、骨科用植體、臉部填充物、口腔頜面及面部手術等)，許多臨床資料已證實它的優異安全性。</li> <li>CMC凝膠體提供了即時的填充與皺紋修復，經由刺激膠原蛋白再生，自體新生的膠原蛋白，可達到長效的持續性填充，最後漸進式被巨噬細胞吸收。</li> <li>PCL微型晶球刺激自體膠原蛋白再生，最後水解成二氧化碳雨水。</li></ul> <picture><source"+(_vm._ssrAttr("srcset",require("assets/img/service/beautyService/ellanse/ellanse-組合2.webp")))+" type=\"image/webp\"> <img"+(_vm._ssrAttr("src",require("assets/img/service/beautyService/ellanse/ellanse-組合2.jpg")))+" alt=\"Ellanse PCL&CMC示意圖\" class=\"w-50\"></picture></div> <div class=\"article-container\"><picture><source"+(_vm._ssrAttr("srcset",require("assets/img/service/beautyService/ellanse/4.webp")))+" type=\"image/webp\"> <img"+(_vm._ssrAttr("src",require("assets/img/service/beautyService/ellanse/4.png")))+" alt=\"PCL\"></picture> <h4>PCL微型晶球隨著時間被身體吸收，自體再生的優質膠原蛋白漸漸填補原來的晶球的空間。</h4></div> <div class=\"article-container\"><h2>Ellanse洢蓮絲 有效性與安全性已被證實</h2> <p>一個為期2年的臨床試驗證實，使用 Ellanse-s 與 ellanse-M 治療法令紋時，有效性與安全性皆達到預期效果。</p> <p>PS.研究者使用全球審美改善標準GAIS(Aesthetic Global lmprovement Scale)</p> <div class=\"img-block\"><div class=\"img-block-item\"><picture><source"+(_vm._ssrAttr("srcset",require("assets/img/service/beautyService/ellanse/5.webp")))+" type=\"image/webp\"> <img"+(_vm._ssrAttr("src",require("assets/img/service/beautyService/ellanse/5.jpg")))+" alt=\"Ellanse洢蓮絲-安全性\"></picture></div> <div class=\"img-block-item\"><picture><source"+(_vm._ssrAttr("srcset",require("assets/img/service/beautyService/ellanse/6.webp")))+" type=\"image/webp\"> <img"+(_vm._ssrAttr("src",require("assets/img/service/beautyService/ellanse/6.jpg")))+" alt=\"Ellanse洢蓮絲-安全性\"></picture></div></div></div> <div class=\"article-container\"><h2>Ellanse洢蓮絲 療程特色</h2> <ul><li>+ 容易施打、容易塑型、立即修復</li> <li>+ 只需要注射一次，可達到在時效內持續性的修復</li> <li>+ 不需調配，立即施打，術後不需按摩</li> <li>+ 可依使用者的偏好，選擇不同時效的劑型</li> <li>+ 再生膠原蛋白可持久填充效果</li> <li>+ 使用者再生的優質膠原蛋白，讓膚質更光滑自然</li> <li>+ 安全性高；可100%被人體吸收代謝的生物輕微刺激填料，並可控制預期的效果</li></ul></div> <div class=\"article-container\"><h2>適用部位</h2> <p>五官雕塑、山根鼻樑雕塑、鼻頭雕塑、蘋果肌雕塑、豐頰、夫妻宮(太陽穴)填補、額頭雕塑、下巴雕塑、皺紋改善、疤痕填補治療、淚溝凹陷治療、眼周細紋改善、眼袋改善、法令紋改善、耳珠耳垂雕塑、緊緻拉提治療、刺激膠原蛋白增生、全臉緊實治療、下垂鬆弛治療、皮膚老化治療、暗沉膚色治療、靜態皺紋治療、橘皮凹凸不平治療等。</p></div> <div class=\"article-container\"><h2>不適用對象</h2> <ul><li>- 患有皮膚炎、特殊皮膚疾病</li> <li>- 孕婦、哺乳中女性</li> <li>- 對藥劑相關成分過敏者</li> <li>- 另蟹足腫體質</li> <li>- 心臟類疾病須經專業醫師評估後始可進行療程</li></ul></div> <div class=\"article-container\"><h2>美麗分享</h2> <h4>天庭高闊 仆馬無窮</h4> <ul><li>+ 治療項目：飽滿額頭及夫妻宮</li> <li>+ 總使用劑量 3ml</li></ul> <picture><source"+(_vm._ssrAttr("srcset",require("assets/img/service/beautyService/ellanse/案例1.webp")))+" type=\"image/webp\"> <img"+(_vm._ssrAttr("src",require("assets/img/service/beautyService/ellanse/案例1.png")))+" alt=\"案例1\"></picture> <p>療程效果會因個人體質而有所差異</p></div> <div class=\"article-container\"><h4>神奇 10ml 跪婦變貴婦</h4> <ul><li>+ 治療項目：眉眼拉提、顴骨飽滿、法令紋改善、下巴雕塑</li> <li>+ 總使用劑量 10ml</li></ul> <picture><source"+(_vm._ssrAttr("srcset",require("assets/img/service/beautyService/ellanse/案例2.webp")))+" type=\"image/webp\"> <img"+(_vm._ssrAttr("src",require("assets/img/service/beautyService/ellanse/案例2.png")))+" alt=\"案例2\"></picture> <p>療程效果會因個人體質而有所差異</p></div> <div class=\"article-container\"><h4>找回同顏蘋果肌 桃花笑顏得人緣</h4> <ul><li>+ 治療項目：圓滿額頭、夫妻宮填補、顴骨飽滿</li> <li>+ 總使用劑量 8ml</li></ul> <picture><source"+(_vm._ssrAttr("srcset",require("assets/img/service/beautyService/ellanse/案例3.webp")))+" type=\"image/webp\"> <img"+(_vm._ssrAttr("src",require("assets/img/service/beautyService/ellanse/案例3.png")))+" alt=\"案例3\"></picture> <p>療程效果會因個人體質而有所差異</p></div> <div class=\"article-container\"><h4>4CC老化下垂OUT</h4> <ul><li>+ 治療項目：夫妻宮填補、眼袋修飾、中臉拉提、下巴雕塑</li> <li>+ 總使用劑量 4ml</li></ul> <picture><source"+(_vm._ssrAttr("srcset",require("assets/img/service/beautyService/ellanse/案例4.webp")))+" type=\"image/webp\"> <img"+(_vm._ssrAttr("src",require("assets/img/service/beautyService/ellanse/案例4.png")))+" alt=\"案例4\"></picture> <p>療程效果會因個人體質而有所差異</p></div> <div class=\"article-container\"><h2>術後小叮嚀</h2> <ul><li>+ 治療後的24小時內，可在接受的位置上用冰袋敷面數分鐘，以減輕腫脹。應避免直接把冰塊放在皮膚上，可用布包裏冰塊敷在治療位置上</li> <li>+ 如無出現任何併發症，可於療程結束數小時後使用化妝品</li> <li>+ 在紅腫消退前，應避免接觸過量的陽光及紫外光燈照射。</li> <li>+ 外出接觸陽光前，應使用SPF30+的防曬用品</li> <li>+ 依照醫師建議口服藥品數日或是擦外用藥膏</li> <li>+ 術後請勿施壓施打部位</li> <li>+ 若有任何不適，請盡快複診</li></ul> <h4>本療程的效果可能因個人體質的不同而有差異，請務必與醫師溝通諮詢</h4></div> <div class=\"article-container\"><h2>Ellanse洢蓮絲 原產認證</h2> <picture><source"+(_vm._ssrAttr("srcset",require("assets/img/service/beautyService/ellanse/原產證.webp")))+" type=\"image/webp\"> <img"+(_vm._ssrAttr("src",require("assets/img/service/beautyService/ellanse/原產證.jpg")))+" alt=\"原廠認證證書\"></picture></div> <div class=\"article-container\"><h2>常見問與答</h2> <h3>Q. 什麼部位適合施打此產品呢</h3> <p>除了眉間，眼皮，及嘴唇不能施打外，全臉臉部有重大凹陷或對臉部立體度不滿意者皆可施打。</p> <h3>Q. 我只想購買1CC，可以打全臉嗎</h3> <p>每個案例在施打拉提及填補凹陷所需的量不同，當劑量不足時，並不適合在全臉拉提的打法，建議可以依最想改善的部位，打倒足夠的量，效果是最佳的。達到預期的效果須施打足夠的量，請讓專業醫師評估所需之劑量。</p> <h3>Q. 打完沒有預期的效果怎麼辦</h3> <p>微粒子刺激自體膠原蛋白增生的時間約到達三個月時最佳狀態，若施打後覺得效果不佳，建議可等待到三個月左右再決定是否再次施打。達到預期的效果須施打足夠的量，請讓專業醫師評估所需之劑量。</p> <h3>Q. 和一般玻尿酸有什麼不同</h3> <p>除了有像玻尿酸可以立即填充外，其主要作用在刺激自體膠原蛋白增生達到治療目的，因次結合填充型和活化型植入劑的優點。</p> <h3>Q. 為什麼坊間有看到價格很低的產品呢</h3> <p>前能有水貨或假貨流竄市面，不止產品品質及運送過程無法保證是否安全，醫師也非原廠訓練之認證醫師，對消費者完全沒有保障。</p></div>")],2)}
